*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;

}
.container{
  /* background-color: dodgerblue; */
  height: 100vh;
 overflow:auto;
 background-color: #1391a2c9;
 /* background-color: #133a89; */

 /* background: rgb(9,121,25);
 background: linear-gradient(90deg, rgba(16, 148, 35, 0.816) 33%,rgb(16, 105, 123) 100%, rgb(16, 105, 123) 100%, rgb(16, 105, 123) 100%); */
 background-size: 400% 400%;
 animation: contant 2s linear;
}
:root{
  --bgcolor:#085b63;
  --color:white;

}
.welcom{
  background-color:var(--bgcolor);
  color:var(--color);
}

@keyframes contant {
  0%{
    background-position: 0%;
  }
  25%{
    background-position: 50%;
  }
  50%{
    background-position: 100%;
  }
  100%{
    background-position: 0%;
  }
  
}
/* 
.anim1{
 justify-content:space-between;
 box-sizing: border-box;

} */




.hid{
  opacity: 0;
  transition: all 5s linear;
transform: translatex(-100%);
}
.show{
  opacity: 1;
  transform: translatex(0);
  /* transition: all 4s linear ; */
  /* box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset; */
  
  
}



.show[id=banana]{
  animation:banana  5s linear;
  opacity: 1;

}
.show[id=apple]{
  animation:apple  5s linear;
  opacity: 1;


}
.show[id=cocknet]{
  animation:cocknet  5s linear;
  opacity: 1;


}
.show[id=baner2]{
  animation: baner2 5s linear ;
  /* opacity: 1; */
  transform-origin: top;
}
#baner2{
  height: 100px;
  padding-left: 100px;
  margin-top: 40px ;
  rotate: calc(-20deg);
 

  
}
 p.show.txt{
  animation: txt 5s linear ;

}

@keyframes txt {
  0%{
  transform: translateX(100%);
  }
  
}

 
   
  

.img1{
  max-height: 300px;
}
@keyframes baner2 {
  0%{
  transform:rotatey(220deg) ;
     
  }
  
}
@keyframes banana {
  0%{
  transform: translateX(-200px);
     
  }
    50%{
      transform: translateX(200px);
         
      }
  
}

@keyframes apple {
  0%{
  transform: translateX(200px);
     
  }
    50%{
      transform: translateX(-200px);
         
      }
  
}
@keyframes cocknet {
  0%{
  transform: translatey(200px);
     
  }
    50%{
      transform: translatey(-100px);
         
      }
  
}

.header{
 animation: header 5s linear;
}

@keyframes header {
  0%{
    transform: rotatex(90deg);
  }
  
}
 
